.product-card {
  position: relative;
  padding: 1rem;
  background-color: white;
  border: 1px solid #ddd;
  border-radius: 10px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  
  
}

.sale-badge {
  position: absolute;
  top: 10px;
  left: 10px;
  background-color: #e63946; /* bright red */
  color: white;
  padding: 5px 10px;
  font-weight: bold;
  font-size: 0.9rem;
  border-radius: 4px;
  z-index: 10;
  box-shadow: 0 0 5px rgba(0,0,0,0.3);
}

.product-card img {
  width: 100%;
  height: 250% !important;
  object-fit: cover;
  border-radius: 6px;
  margin-bottom: 10px;
}
.product-card button {
  padding: 0.5rem 1rem;
  background: black;
  color: white;
  border: none;
  margin-top: 0.5rem;
  cursor: pointer;
}

.product-card button:hover {
  background: #444;
}

.new-collection-tag {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: #7d7262; /* your theme color */
  color: white;
  padding: 5px 8px;
  font-size: 12px;
  font-weight: bold;
  border-radius: 4px;
  z-index: 10;
}